
#chat-holder {
    width: 99%;
    height: 97%;
    margin: 10px auto;
    align-content: center;
    position: relative;
}

#messages-list {
    list-style: none;
    margin: 0;
    padding: 10px 0;
    height: 80%;
    overflow-y: scroll;
    -ms-overflow-style: none;  
    scrollbar-width: none;  
}

#messages-list::-webkit-scrollbar {
    display: none;
}

.message-container {
    width: 100%;
    margin-top: 10px;
}

.username-label {
    margin: 5px 0 0 12px;
    font-size: 80%;
    display: inline;
}

.mute {
    display: inline;
    font-size: 80%;
    margin-left: 3px;
    background: rgb(41, 41, 41);
    color: white;
    border-radius: 20px;
    padding: 3px 6px;
}

.mute:hover {
    cursor: pointer;
}

.chat-message {
    word-break: break-word;
    max-width: 90%;
    width: fit-content;
    background: rgb(0, 0, 100);
    color: white;
    border-radius: 10px;
    margin: 3px auto 0 5px;
    padding: 7px;
}

#message-form {
    border-top: 1px solid rgb(180, 180, 180);
    position: absolute;
    bottom: 0;
    width: 100%;
    padding: 0;
}

#message-input {
    width: 85%;
    font-size: 18px;
    margin: 0;
    margin-top: 3px;
    padding: 0;
    padding-left: 2%;
    border: 0;
    background: rgb(230, 230, 230);
}

#message-submit {
    width: 25px;
    height: 25px;
    padding: 5px;
    border: 0;
    border-radius: 100px;
    background: rgb(0, 0, 100);
    color: white;
    position: absolute;
    right: 5px;
    bottom: 0;
    margin-bottom: -5px;
}

#message-submit-image {
    width: 100%;
    height: 100%;
}

#message-submit:hover {
    cursor: pointer;
}

#empty-chat-message {
    width: 95%; 
    margin: 10px auto;
    font-size: 20px;
    text-align: center;
}