#signed-in-container {
    width: 90vw;
    margin: 10px auto;
    align-content: center;
    text-align: center;
    padding: 5vh 0;
}

.signed-in-button {
    width: 90%;
    max-width: 500px;
    height: 40px;
    display: block;
    margin: 10px auto;
    border: 1px solid rgb(165, 165, 165);
    border-radius: 100px;
    font-size: 150%;
    background: silver;
}

.signed-in-button:hover {
    cursor: pointer;
    color: white;
    background: rgb(0, 0, 0);
}

.delete-confirmation-input {
    width: 90%;
    max-width: 500px;
    height: 40px;
    display: block;
    margin: 10px auto;
    border: 1px solid rgb(165, 165, 165);
    border-radius: 100px;
    font-size: 150%;
    text-align: center;
}

.delete-account {
    color: white;
    background: rgb(224, 0, 0);
}

#delete-toggle {
    color: blue;
    text-decoration: underline;
    width: fit-content;
    margin: 10px auto 0;
}

#delete-toggle:hover {
    cursor: pointer;
}